<template>
  <div>
    <el-form>
      <el-form-item
        label="用户名"
        label-width="60px"
      >
        <el-input
          v-model="formData.name"
          style="width:120px"
        />
      </el-form-item>
      <el-form-item
        label="密码"
        label-width="60px"
      >
        <el-input
          v-model="formData.password"
          style="width:120px"
        />
      </el-form-item>
      <el-button @click="login">
        登录
      </el-button>
    </el-form>
    <el-upload
      :limit="3"
      action="#"
      :file-list="fileList"
      :on-remove="handleRemove"
      :on-change="changeImage"
      :before-upload="beforeUpload"
      :http-request="upload"
    >
      <i class="el-icon-plus" />
    </el-upload>
    <el-button @click="getDocList">
      获取文件
    </el-button>
    <el-button @click="downloadDocument">
      下载文件
    </el-button>
  </div>
</template>

<script>
import {
  login
} from "@/api/login"
import {
  uploadFile
} from "@/api/upload"
import {
  getDocList, downloadDocument
} from "@/api/getDocList"
export default {
  name: "DiskHome",
  data() {
    return {
      downloadData: {
        name: "cloud-disk/repo:1/8a78d92e-171e-4c35-a3e8-7f619a448457.jpg",
        fileName: "武侠"
      },
      formData: {
        name: "liuyt",
        password: "liuyt777"
      },
      fileList: [{
        name: "food.jpeg",
        url: "https://fuss10.elemecdn.com/3/63/4e7f3a15429bfda99bce42a18cdd1jpeg.jpeg?imageMogr2/thumbnail/360x360/format/webp/quality/100"
      }, {
        name: "food2.jpeg",
        url: "https://fuss10.elemecdn.com/3/63/4e7f3a15429bfda99bce42a18cdd1jpeg.jpeg?imageMogr2/thumbnail/360x360/format/webp/quality/100"
      }]
    }
  },
  methods: {
    async login() {
      const res = await login(this.formData)
      const {
        data
      } = res
      this.$store.commit("setUser", data.data)
      console.log(this.$store.state)
      console.log(res)
    },
    handleRemove(file, fileList) {
      console.log("删除回调", file, fileList)
    },
    changeImage(file, fileList) {
      console.log("改变回调", file, fileList)
    },
    beforeUpload(file) {
      console.log("上传前回调", file)
    },
    async upload(params) {
      console.log("上传中", params)
      if(params.file) {
        const res = await uploadFile({
          parentId: "10",
          file: params.file
        })
        console.log("后端返回数据", res)
      }
      console.log("成功")
    },
    async getDocList() {
      const res = await getDocList("5")
      console.log(res)
    },
    async downloadDocument() {
      const res = await downloadDocument(this.downloadData)
      console.log(res)
    }
  }
}
</script>

  <style lang="scss" scoped>

  </style>

